body, button {
  font-family: 'Annie Use Your Telescope', cursive;
}

/* HEADERS */
h1, h2, h3, h4, h5, h6 {
  font-family: 'Reenie Beanie', cursive;
  letter-spacing: 5px;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.5rem;
}

header {
  text-align: center;
  margin: 3rem 0
}

/* SPLASH PAGE */
.intro {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* BANNER */
button {
  margin: 0 auto;
  display: block;
  padding: 1rem;
  font-size: 1.8rem;
  letter-spacing: 0.5rem;
  height: 5rem; /* TODO: there's probably a better way to handle shifting page content down under a fixed position banner */
}

.meal {
  position: fixed;
  top: 0;
  width: 100%;
}

/* MAIN CONTENT */
.hide {
  display: none;
}

.show {
  display: block;
}

.meal-root {
  padding-top: 5rem;
}

/* IMAGE */
.img-wrapper {
  float: left;
  padding-left: 4rem;
  padding-right: 4rem;
  margin-bottom: 3rem;
}

img {
  max-width: 100%;
  box-shadow: 1px 1px 3px 1px rgba(0,0,0,0.25);
}

/* DIRECTIONS */
p {
  font-size: 2rem;
  letter-spacing: 1px;
  padding-right: 4rem;
  padding-bottom: 1rem;
  padding-left: 4rem;
  line-height: 2;
}

/* VIDEO */
.media {
  margin: 0 auto 1rem auto;
  padding-top: 2rem;
  text-align: center;
  clear: both;
}

/* INGREDIENTS */
.ingredients {
  display: grid;
  grid: auto / repeat(auto-fit, 12rem);
  justify-content: center;
  grid-gap: 2rem;
  padding: 4rem;
}

/* FOOTER */
footer {
  margin-top: 2rem;
  font-size: 1.5rem;
  padding: 1rem;
}